import '@fancyapps/fancybox';
import createVideoJs from './createVideoJs';

let thumbsPs = null;

$('[data-fancybox]')
	.fancybox({
		buttons: [
			'zoom',
		],
		backFocus: false,
		modal: false,
		infobar: true,
		image: {
			preload: true,
		},
		baseTpl:
			$('.fancybox_tpls .baseTpl').html(),
		video: {
			tpl:
				`<div class="fancybox-content">
                    <div class="fancy__video">
                        <video class="video video-js js-video" controls controlsList="nodownload" poster="{{poster}}">
                            <source src="{{src}}" type="{{format}}" />
                        </video>
                    </div>
                </div>`,
			format: '',
			autoStart: false,
		},
		btnTpl: {
			zoom:
				$('.fancybox_tpls .btnTpl_zoom').html(),

			// Arrows
			arrowLeft:
				$('.fancybox_tpls .btnTpl_arrowLeft').html(),

			arrowRight:
				$('.fancybox_tpls .btnTpl_arrowRight').html(),
		},
		thumbs: {
			autoStart: true,
			hideOnClose: true,
			parentEl: '.fancy__thumbs',
			axis: 'x',
		},
		onInit: function (instance, current) {
		},
		beforeLoad: function(instance, current) {
			let $fancyBox = $('.fancybox-container');
			let $fancyThumbs = $fancyBox.find('.fancy__thumbs .fancybox-thumbs');
		},
		afterLoad: function (instance, current) {
			let $currentContent = $(current.$content);

			if (current.type === 'video') {
				let $video = $currentContent.find('.js-video');

				if ($(current.opts.$orig)
					.data('audio')) {
					$video.addClass('video_audio');
				}

				createVideoJs($video[0]);
			}
		},
		beforeShow: function (instance, current) {
			let $clickedElement = $(current.opts.$orig);
			let currentCaption = ($clickedElement.data('caption') || '') +
				$.trim($clickedElement
					.find('.js-data-caption')
					.html() || '');

			let $fancyBox = $('.fancybox-container');
			let $fancy = $fancyBox.find('.fancy');
			let $captionContainer = $fancyBox.find('.js-caption');

			$captionContainer.html(currentCaption);

			if (currentCaption.length === 0) {
				$fancy.addClass('fancy_no-caption');
			} else {
				$fancy.removeClass('fancy_no-caption');

				$captionContainer.find('.js-video')
					.each((index, element) => {
						if ($(element)
							.data('audio')) {
							$(element)
								.addClass('video_audio');
						}
						createVideoJs($(element)[0]);
					});

				if (thumbsPs) {
					thumbsPs.update();
				}
			}
		},
		afterClose: function () {

			if (thumbsPs) {
				thumbsPs.destroy();
				thumbsPs = null;
			}
		},
	});
