function initComponent(component) {
	let $component = $(component);

	if ($component.hasClass('is-inited')) return;
	$component.addClass('is-inited');

	let $collapseHead = $component.find('.js-collapse-head');
	let $collapseContent = $component.find('.js-collapse-content');
	let $scroller = $('.js-scroller');

	$collapseContent.collapse({
		parent: $component,
		toggle: false,
	});

	setTimeout(() => {
		$collapseContent.stop(true, true)
			.removeClass('show');
        $('.js-collapse-content.show_on_load').addClass('show');  
		$component.stop(true, true)
			.addClass('collapse-list_show');
		$(document.body)
			.trigger('scroll:update');
	}, 30);

	$collapseContent.on('hide.bs.collapse', (e) => {
		let $trgt = $(e.target);
		let $item = $trgt.parent();
		let $head = $item.find('.js-collapse-head');

		$head.removeClass('collapse-head_active');
		$item.removeClass('collapse-item_active');
	});

	$collapseContent.on('show.bs.collapse', (e) => {
		let $trgt = $(e.target);
		let $item = $trgt.parent();
		let $head = $item.find('.js-collapse-head');

		$head.addClass('collapse-head_active');
		$item.addClass('collapse-item_active');
	});

	$collapseContent.on('shown.bs.collapse hidden.bs.collapse', () => {
		$(document.body)
			.trigger('scroll:update');
	});

	$collapseContent.on('shown.bs.collapse', (e) => {
		$scroller.animate({
			scrollTop: $(e.target).parent().find('.js-collapse-head').offset().top,
		}, 500);
	});

	$collapseHead.on('click', (e) => {
		e.preventDefault();

		$(e.currentTarget)
			.closest('.js-collapse-item')
			.find('.js-collapse-content')
			.collapse('toggle');
	});
}

function destroyComponent(component) {
    $(component).removeClass('is-inited');
}

$('.js-collapse-list')
	.each((listIndex, component) => {
		initComponent(component);
	});

$(window).on('page:change', () => {
	$('.js-collapse-list')
		.each((listIndex, component) => {
			destroyComponent(component);
			initComponent(component);
		});
});
