function initComponent(component) {
	let $component = $(component);

	if ($component.hasClass('is-inited')) return;
	$component.addClass('is-inited');

	let $body = $(document.body);
	let $closeBtn = $component.find('.js-popup-close');

	$(document.body)
		.on('pdf:show', () => {
			$body.trigger('body:show-popup');
			$component.fadeIn(200);
		});

	$(document.body)
		.on('pdf:hide', () => {
			$body.trigger('body:hide-popup');
			$component.fadeOut(200);
		});

	$closeBtn.on('click', (e) => {
		e.preventDefault();

		$(document.body)
			.trigger('pdf:hide');
	});
}

$('.js-pdf-popup')
	.each((index, component) => {
		initComponent(component);
	});

$(window).on('page:change', () => {
	$('.js-pdf-popup')
		.each((index, component) => {
			initComponent(component);
		});
});

$(document.body)
	.on('click', '.js-material-item-document', (e) => {
		e.preventDefault();

		let $trgt = $(e.currentTarget);
		let embedStr = `<embed src="${$trgt.attr('href')}" type="application/pdf" width="100%" height="100%"/>`;

		$('.js-pdf-popup')
			.find('.js-embed')
			.html(embedStr);
		$(document.body)
			.trigger('pdf:show');
	});
