$(window).on('body:show-popup', () => {
	let $body = $(document.body);

	$body.addClass('show-popup');
});

$(window).on('body:hide-popup', () => {
	let $body = $(document.body);

	$body.removeClass('show-popup');
});

$(window).on('page:change', () => {
	console.log('page changed');
});
