$(document.body).on('click', '.js-card', (e) => {
	e.preventDefault();

	let $component = $(e.currentTarget);
	let $body = $(document.body);

	if($component.hasClass('show_in_menu')){
		let trgt_id = $component.data('id');
		$body.trigger('select_menu_element', [trgt_id]);
		$body.trigger('main-menu:open', []);
	}else{
		let trgt_href = $component.attr('href');
	}
});
